import Vue from 'vue'
export default {
	data() {
	return {
			serviceList: '',
			userInfo: null,
			PageCur: 'basics',
			
			currentOrder: null,
			busyStatus: 0,
			
			
			page: 1,
			totalPage: null,
			orderList: [],
			orderStatus: 0
		}
	},
	onLoad() {
		uni.showLoading({
			
		})
		this.GetOrderList()
		this.GetCurrentOrder()
		this.GetStaffInfo()
		this.GetServiceList()
		
		
	},
	methods: {
		MineGoOrderList(){
			this.PageCur = 'component'
		},
		GetOrderList(){
			uni.showLoading({
				
			})
			var that = this
			uni.request({
				header: {token: that.Token},
				url: that.Server + 'order/list',
				data: {
					page: that.page,
					status: that.orderStatus,
					size: 10
				},
				success(res) {
					uni.hideLoading()
					if(res.data.code == 0){
						that.totalPage = res.data.data.totalPage
						for(var i in res.data.data.list){
							that.orderList.push(res.data.data.list[i])
						}
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: "none",
							title: '登录超时，将重新授权登录',
							duration: 1000
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 2000)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 1000
						})
					}
				}
			})
		},
		GetServiceList(){
			var that = this
			uni.request({
				header: {token: that.Token},
				url: that.Server + 'merchantProject/list',
				success(res) {
					uni.hideLoading()
					if(res.data.code == 0){
						that.serviceList = res.data.data
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: "none",
							title: '登录超时，将重新授权登录',
							duration: 1000
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 2000)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 1000
						})
					}
				}
			})
		},
		GetStaffInfo(){
			
			var that = this
			uni.request({
				header: {token: that.Token},
				url: that.Server + 'staff/info',
				success(res) {
					uni.hideLoading()
					if(res.data.code == 0){
						that.userInfo = res.data.data
						res.data.data.imgs = JSON.parse(res.data.data.imgs)
						Vue.prototype.UserInfo = res.data.data
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: "none",
							title: '登录超时，将重新授权登录',
							duration: 1000
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 2000)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 1000
						})
					}
				}
			})
		},
		ChangeStatus(data){
			var that = this
			uni.request({
				header: {token: that.Token},
				url: that.Server + 'staff/updStatus',
				data: {
					status: data.targetStatus,
				},
				success(res) {
					uni.hideLoading()
					if(res.data.code == 0){
						uni.showToast({
							icon: 'none',
							title: data.targetStatus == true && data.workStatus == 0 ? '取消预约成功' : data.targetStatus == true && data.workStatus == 1 ? '提前下钟成功' : data.targetStatus == false ? '已改变为忙碌状态' : ''
						})
						that.GetStaffInfo()
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: "none",
							title: '登录超时，将重新授权登录',
							duration: 1000
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 2000)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 1000
						})
					}
				}
			})
			
		},
		CancelOrder(data){
			var that = this
			console.log(data);
			uni.showModal({
				content: data.targetStatus == false ? '确定已被预约吗？' : '确定已经空闲吗？',
				success(res) {
					if(res.confirm){
						if(data.targetStatus == true && data.workStatus == 0 && data.id != undefined){
							uni.request({
								header: {token: that.Token},
								url: that.Server + 'order/cancel',
								data: {
									orderId: data.id,
								},
								success(res) {
									uni.hideLoading()
									if(res.data.code == 0){
										that.ChangeStatus(data)
										
									} else if (res.data.code == 101) {
										uni.showToast({
											icon: "none",
											title: '登录超时，将重新授权登录',
											duration: 1000
										})
										setTimeout(() => {
											uni.reLaunch({
												url: '../login/login'
											})
										}, 2000)
									} else {
										uni.showToast({
											icon: "none",
											title: res.data.msg,
											duration: 1000
										})
									}
								}
							})
						} else if ( data.targetStatus == true && data.workStatus == 0 && data.id == undefined ) {
							that.ChangeStatus(data)
						}  else if ( data.targetStatus == false ) {
							that.ChangeStatus(data)
						}
						
					}
				}
			})
		},
		GetCurrentOrder(){
			var that = this
			uni.request({
				header: {token: that.Token},
				url: that.Server + 'order/currOrder',
				success(res) {
					uni.hideLoading()
					if(res.data.code == 0){
						that.currentOrder = res.data.data
						res.data.data.totalCount == 0 ? that.busyStatus = 0 : that.busyStatus = 1
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: "none",
							title: '登录超时，将重新授权登录',
							duration: 1000
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 2000)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 1000
						})
					}
				}
			})
		},
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		}
	}
}
